	module.exports = {"data":{"site":{"siteMetadata":{"title":"Justin Ball","author":"Justin Ball"}},"markdownRemark":{"id":"/Users/jbasdf/projects/justinball/src/posts/2008-12-20-the-binding-argument-of-concat-is-no-longer-needed/index.md absPath of file >>> MarkdownRemark","html":"<p>If you upgrade to Rails 2.2.2 and start getting \"The binding argument of #concat is no longer needed\"  then wherever you do something like this:</p>\n<pre><code class=\"ruby\">\nconcat(content, block.binding)\n</pre></code>\n<p>change it to this:</p>\n<pre><code class=\"ruby\">\nconcat(content)\n</pre></code>\n<p>The 'block.binding' argument is no longer needed which is nice because who really wanted to type that over and over again anyway.</p>","frontmatter":{"title":"The binding argument of #concat is no longer needed","date":"December 20, 2008","imageUrl":null,"structuredHtml":null,"image":null}}},"pathContext":{"path":"/the-binding-argument-of-concat-is-no-longer-needed"}}

